body {
  background: linear-gradient(to right, rgb(13, 109, 168), rgb(255, 149, 219));
  margin: 0;
  font-family: 'Linux Biolinum Bold';
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 1rem;
  gap: 2rem;
  grid-auto-rows: 1fr;
  grid-auto-columns: 1fr;
}

.container {
  margin: 2rem 2rem;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  background-color: beige;
  padding: 0 2rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.4);
}

.form-group {
  margin: .5rem;
  display: flex;
  flex-direction: column;
}

.form-group > label {
  color: #777;
  font-size: .75rem;
  margin-bottom: .25rem;
}

.card {
  position: relative;
  display: flex;
}

.card .front:hover,
.card .back:hover {
  --translate-y: -3px;
  box-shadow: 0px 0 5px 5px rgba(0, 0, 0, 0.4);
}

.card.flip {
  --rotate-y: 180deg;
}

.card .front,
.card .back {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  padding: 1.5rem;
  backface-visibility: hidden;
  border-radius: 0.25rem;
  box-shadow: 0 0 5px 2px black;
  background-color: rgb(240, 236, 236);
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
}

.card .back {
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  top: 0;
  left: 0;
}

.card.flip .front,
.card .back {
  transform: perspective(1000px) rotateY(180deg)
    translateY(var(--translate-y, 0));
}

.card.flip .back,
.card .front {
  transform: perspective(1000px) rotateY(0) translateY(var(--translate-y, 0));
}

.flashcard-options {
  margin-top: 0.5rem;
}

.flashcard-option {
  margin-top: 0.25rem;
  color: #555;
  font-size: 0.75rem;
}

.flashcard-option:first-child {
  margin-top: 0px;
}

.glow-on-hover {
  width: 80px;
  height: 30px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:active {
  color: #000;
}

.glow-on-hover:active:after {
  background: transparent;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@font-face {
  font-family: 'Linux Biolinum Bold';
  font-style: normal;
  font-weight: normal;
  src: local('Linux Biolinum Bold'), url('./LinBiolinum_RB.woff') format('woff');
}
